}
.vux-slider > .vux-indicator-left {
  left: 15px;
  right: auto;
}
.vux-slider > .vux-swiper {
  overflow: hidden;
  position: relative;
}
.vux-slider > .vux-swiper > .vux-swiper-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
